.market-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    color: white;
  }
  
  .market-table th,
  .market-table td {
    padding: 10px;
    border: 1px solid #2c2f36;
    text-align: left;
  }
  
  .market-table th {
    background-color: #2c2f36;
  }
  
  .market-table tr:hover {
    background-color: #2a3b58;
  }
  