.sidebar {
    width: 200px;
    background-color: #14161a;
    color: white;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    padding-top: 20px;
  }
  
  .sidebar ul {
    list-style: none;
    padding: 0;
  }
  
  .sidebar li {
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .sidebar li:hover {
    background-color: #2c2f36;
  }
  