/* Container untuk Candlestick Chart */
.candlestick-chart {
    margin: 20px auto;
    padding: 20px;
    max-width: 800px;
    border: 1px solid #2c2f36;
    border-radius: 10px;
    background-color: #1e2128;
    color: #ffffff;
  }
  
  .candlestick-chart h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #fcd535;
    font-size: 1.5em;
  }
  