.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #14161a;
    color: white;
    padding: 10px 20px;
  }
  
  .nav a {
    margin: 0 15px;
    color: white;
    text-decoration: none;
  }
  
  .nav a:hover {
    color: #fcd535;
  }
  
  .connect-wallet {
    background-color: #fcd535;
    color: black;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
  }
  