.App {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-container {
  display: flex;
  margin-top: 60px;
}

.content {
  margin-left: 200px;
  padding: 20px;
  flex: 1;
  background-color: #181a20;
  min-height: calc(100vh - 60px);
}
